<template>
    <div class='privatecourse'>
        <div class='title'>课程总览</div>
        <div class='courseInfo'>
            <div class='condition'>
                <div class='block'>
                    <table cellpadding="0" cellspacing="0" id="1">
                      <tr>
                        <td>
                            <div id="cal">
                                    <div id="top">
                                        <div class="select">
                                            <select id="year-select"></select>&nbsp;<span></span>
                                            <select id="month-select"></select>&nbsp;<span>月</span>
                                        </div>
                                        <div class="step">
                                            <span id="prev"><img src="../../assets/images/addvipkind/btn_time_left_press.png"/></span>
                                            <span id="next"><img src="../../assets/images/addvipkind/btn_time_right_press.png"/></span>
                                        </div>
                                    </div>
                                    <ul id="wk">
                                        <li>一</li>
                                        <li>二</li>
                                        <li>三</li>
                                        <li>四</li>
                                        <li>五</li>
                                        <li><b>六</b></li>
                                        <li><b>日</b></li>
                                    </ul>
                                    <div id="cm"></div>
                                    <!-- <div id="bm">
                                        <div class="heavenly-branch">
                                            <span id="heavenly"></span>年 &nbsp;
                                            ［<span id="branch"></span>］
                                        </div>
                                        <a href="javascript:;;" id="now-date">回到今天</a>
                                    </div> -->
                                </div>
                        </td>
                    </tr>
                </table>
                </div>
                <div class='text'>
                    筛选门店：
                </div>
                <div class='block'>
                    <el-select v-model="branch" placeholder="请选择" clearable @change="getbranch($event)">
                        <el-option
                        v-for="(item, index) in branchlist"
                        :key="index"
                        :label="item.name"
                        :value="item.id"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                    </el-select>
                </div>
                <div class='text coach'>
                    筛选教练：
                </div>
                <div class='block'>
                    <el-select v-model="coach" placeholder="请选择" clearable  @change="getchangecoach($event)">
                        <el-option
                        v-for="(item, index) in coachlist"
                        :key="index"
                        :label="item.coachName"
                        :value="item.coachId"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.coachName}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                    </el-select>
                </div>
                <div class='btn'>
                    <div class='query' @click='query()'>筛选</div>
                </div>
            </div>
            <div class='courseTable'>
                <div class='title'>筛选条件下的周合计课程数：<span>{{sum}}节</span></div>
                <div class='tabHead'>
                    <li v-for='(item,index) in dateList' :key='index' :class="{firstLi: index === 0 ? true : false}">
                        <div class='dateWeek'>
                            <div>{{item.date}}</div>
                            <div>{{item.week}}</div>
                        </div>
                    </li>
                </div>
                <!-- <div class='tableBody'>
                    <div class='timeList'>
                        <li v-for='(item,index) in timeList' :key='index' class='time' :class="{whiteColor: index % 2 === 0 ? true : false}">{{item}}</li>
                    </div>
                    <div class='courselist'>
                        <li v-for='item in resultList' :key='item.index'>
                            <div v-for='(itemChild,index) in item.result1' :key='index' class='itemChild' :class="{whiteColor: index % 2 === 0 ? true : false}">
                                <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                                <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                            </div>
                            {{item.index}}
                        </li>
                    </div>
                </div> -->
                <div class='tableBody'>
                  <div class='timeList'>
                    <div v-for='(item,index) in timeList' :key='index' class='time' :class="{whiteColor: index % 2 === 0 ? true : false}">
                      <div class='rultlist time-fir'>{{item.time}}</div>
                      <div class='rultlist'>
                        <li v-for='(itemChild) in item.result1' :key="itemChild.index">
                          <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                        </li>
                      </div>
                     <div class='rultlist'>
                        <li v-for='(itemChild) in item.result2' :key="itemChild.index">
                         <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                        </li>
                     </div>
                     <div class='rultlist'>
                       <li v-for='(itemChild) in item.result3' :key="itemChild.index">
                        <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                      </li>
                     </div>
                     <div class='rultlist'>
                        <li v-for='(itemChild) in item.result4' :key="itemChild.index">
                          <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                        </li>
                     </div>
                     <div class='rultlist'>
                        <li v-for='(itemChild) in item.result5' :key="itemChild.index">
                          <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                        </li>
                     </div>
                     <div class='rultlist'>
                        <li v-for='(itemChild) in item.result6' :key="itemChild.index">
                          <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                        </li>
                     </div>
                     <div class='rultlist'>
                        <li v-for='(itemChild) in item.result7' :key="itemChild.index">
                          <div v-if='itemChild.coachName || itemChild.branchName'>{{itemChild.appointmentTime | getMinuter}}</div>
                          <div>{{itemChild.coachName}}<span v-if='itemChild.branchName'>（{{itemChild.branchName}}）</span></div>
                        </li>
                     </div>
                    </div>
                  </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {getallbranch, getbranchcoach, getcourse} from '../../services/service.js'
import {isApiResponseOk} from '../../utils/com.js'
export default {
  data () {
    return {
      branchlist: [
      ],
      branch: '',
      coach: '',
      coachlist: [],
      // timeList: [
      //   '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00',
      //   '20:00', '21:00', '22:00', '23:00'
      // ],
      timeList: [
        {time: '6:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '7:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '8:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '9:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '10:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '11:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '12:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '13:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '14:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '15:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '16:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '17:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '18:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '19:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '20:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '21:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '22:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []},
        {time: '23:00', result1: [], result2: [], result3: [], result4: [], result5: [], result6: [], result7: []}
      ],
      dateList: [
        {date: '', week: ''},
        {date: '', week: '周一'},
        {date: '', week: '周二'},
        {date: '', week: '周三'},
        {date: '', week: '周四'},
        {date: '', week: '周五'},
        {date: '', week: '周六'},
        {date: '', week: '周日'}
      ],
      resultList: [
        { result1: [] },
        { result1: [] },
        { result1: [] },
        { result1: [] },
        { result1: [] },
        { result1: [] },
        { result1: [] }
      ],
      prev: '<',
      next: '>',
      para: {startLongTime: '', endLongDate: '', branchId: '', personId: ''},
      sum: 0,
      timeStatus: 60
    }
  },
  mounted () {
    let self = this
    this.$nextTick(function () {
      window.calendar.init({
        cellClickCallback: function (cell, datedata) {
          let date = datedata.solarYear + '-' + datedata.solarMonth + '-' + datedata.solarDate
          console.log(datedata.solarYear + '-' + datedata.solarMonth + '-' + datedata.solarDate)
          self.initDate(date)
          // alert('你点击的是' + datedata.solarYear + '年' + datedata.solarMonth + '月' + datedata.solarDate + '日')
        }
      })
      // 给表格复制
      for (var i = 0; i < this.resultList.length; i++) {
        this.resultList[i].result1 = []
        for (var j = 6; j < 24; j++) {
          this.resultList[i].result1.push({appointmentTime: '', branchName: '', coachName: ''})
        }
      }
      console.log('获取数据', this.resultList)
    })
  },
  created () {
    // 获取所有分店信息
    this.initbranch()
    // 获取今天的日期
    this.initDate()
    // 获取教练
    this.getCoach()
    // 获取整点半点的情况
    // this.initgym()
    // let oToday = new Date()
    // // 获取一周的所有的日期
    // let currentDay = oToday.getDay()
    // if (currentDay === 0) { currentDay = 7 }
    // let mondayTime = oToday.getTime() - (currentDay - 1) * 24 * 60 * 60 * 1000
    // let sundayTime = oToday.getTime() + (7 - currentDay) * 24 * 60 * 60 * 1000
    // let monday = mondayTime
    // let sunday = sundayTime
    // this.para.startTime = monday
    // this.para.endDate = sunday
    // console.log(currentDay, this.getWeek(0, oToday), this.getWeek(1, oToday), this.getWeek(2, oToday), this.getWeek(3, oToday))
    // this.dateList[1].date = this.getWeek(0, oToday)
    // this.dateList[2].date = this.getWeek(1, oToday)
    // this.dateList[3].date = this.getWeek(2, oToday)
    // this.dateList[4].date = this.getWeek(3, oToday)
    // this.dateList[5].date = this.getWeek(4, oToday)
    // this.dateList[6].date = this.getWeek(5, oToday)
    // this.dateList[7].date = this.getWeek(6, oToday)
    // 获取所有的课程
    this.initcourse(this.para)
  },
  methods: {
    getWeek (i, time) {
      var now = new Date(time)
      var firstDay = new Date(now - (now.getDay() - 1) * 86400000)
      firstDay.setDate(firstDay.getDate() + i)
      let mon = Number(firstDay.getMonth()) + 1
      return now.getFullYear() + '-' + mon + '-' + firstDay.getDate()
    },
    // 获取日期
    initDate (date) {
      let oToday = ''
      if (!date) {
        oToday = new Date()
      } else {
        oToday = new Date(date)
      }
      let currentDay = oToday.getDay()
      if (currentDay === 0) { currentDay = 7 }
      let mondayTime = oToday.getTime() - (currentDay - 1) * 24 * 60 * 60 * 1000
      let sundayTime = oToday.getTime() + (7 - currentDay) * 24 * 60 * 60 * 1000
      let monday = mondayTime
      let sunday = sundayTime
      this.para.startLongTime = monday
      this.para.endLongDate = sunday
      console.log(currentDay, this.getWeek(0, oToday), this.getWeek(1, oToday), this.getWeek(2, oToday), this.getWeek(3, oToday))
      this.dateList[1].date = this.getWeek(0, oToday)
      this.dateList[2].date = this.getWeek(1, oToday)
      this.dateList[3].date = this.getWeek(2, oToday)
      this.dateList[4].date = this.getWeek(3, oToday)
      this.dateList[5].date = this.getWeek(4, oToday)
      this.dateList[6].date = this.getWeek(5, oToday)
      this.dateList[7].date = this.getWeek(6, oToday)
    },
    getDate () {
      console.log('shihjian')
    },
    async initbranch () {
      const res = await getallbranch()
      if (isApiResponseOk(res)) {
        if (res.data.length > 0) {
          this.branchlist = res.data
        }
        console.log('获取所有分店信息', res)
      }
    },
    getbranch (branch) {
      console.log('获取分店', branch)
      // 获取分店下的教练
      this.branch = branch // 表示用户已经选择了分店，才能选择教练，否则会给用户提示
      this.para.branchId = branch
      console.log('huoqu branch', branch)
      this.getCoach(branch)
    },
    async getCoach (data) {
      const res = await getbranchcoach({branchId: data})
      if (isApiResponseOk(res)) {
        this.coachlist = res.data
      }
    },
    // 获取教练回调
    getchangecoach (coach) {
      this.para.personId = coach
      console.log('获取教练')
    },
    // 筛选信息
    query () {
      console.log('获取教练的信息', this.para)
      // this.initcourse(this.para)
      for (var i = 0; i < this.timeList.length; i++) {
        this.timeList[i].result1 = []
        this.timeList[i].result2 = []
        this.timeList[i].result3 = []
        this.timeList[i].result4 = []
        this.timeList[i].result5 = []
        this.timeList[i].result6 = []
        this.timeList[i].result7 = []
      }
      this.initcourse(this.para)
    },
    // 获取课程信息
    async initcourse (data) {
      console.log('获取课程信息', this.resultList)
      const res = await getcourse(data)
      if (isApiResponseOk(res)) {
        if (res.data && res.data.length > 0) {
          let list = res.data
          this.sum = res.data.length
          for (var i = 0; i < list.length; i++) {
            if (list[i] && list[i].appointmentTime) {
              let date = new Date(list[i].appointmentTime).getDay()
              let hours = new Date(list[i].appointmentTime).getHours()
              this.cellTable(hours, date, list[i])
              console.log(date)
            }
          }
          console.log('获取消息', res.data, this.resultList[5])
        } else {
        }
      }
    },
    // 填充表格
    cellTable (hours, date, list) {
      let cel = hours - 6
      if (date === 1) {
        this.timeList[cel].result1.push(list)
      } else if (date === 2) {
        this.timeList[cel].result2.push(list)
      } else if (date === 3) {
        this.timeList[cel].result3.push(list)
      } else if (date === 4) {
        this.timeList[cel].result4.push(list)
      } else if (date === 5) {
        this.timeList[cel].result5.push(list)
      } else if (date === 6) {
        this.timeList[cel].result6.push(list)
      } else if (date === 7) {
        this.timeList[cel].result7.push(list)
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.privatecourse
    li
        list-style: none;
    .title
        font-size: .24rem;
        color: #263238;
    .courseInfo
        display: flex;
    .condition
        width: 3.3rem;
        .el-date-picker
            position: absolute !important;
        .text
            font-size: .14rem;
            color: #263238;
            margin-top: .5rem;
        .coach
            margin-top: .3rem;
        .btn
            display: flex;
            justify-content: center;
            margin-top: 1.44rem;
            .query
                background: linear-gradient(to right, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
                border-radius: .04rem;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 1.2rem;
                height: .36rem;
                font-size: .13rem;
                color: #FFFFFF;
                cursor: pointer;
    .courseTable
        flex: 2;
        margin-left: .68rem;
        margin-bottom: .5rem;
        .title
            font-size: .14rem;
            color: #909399;
            margin-top: .2rem;
            margin-bottom: .2rem;
            span
                font-size: .16rem;
                color: #263238;
        .time
            display: table;
            width: 100%;
            text-align: center;
            min-height: .6rem;
            align-items: center;
            font-size: .11rem;
            color: #9198A6;
            font-size: .11rem;
            color: #263238;
            background: rgba(251, 252, 253, .3);
            border-right: 1px solid #EAECEF;
        .whiteColor
            background: #fff;
            border-bottom: 1px solid #eaeef0;
        .tabHead
            display: flex;
            li
                flex: 1;
                text-align: center;
                font-size: .11rem;
                color: #8197A3;
                height: .5rem;
                background: #EAEEF0;
                box-shadow: inset 0 -.06rem .18rem 0 rgba(209,220,225,0.20);
                .dateWeek
                    display: flex;
                    flex-flow: column;
                    justify-content: center;
                    height: .5rem;
            .firstLi
                width: .91rem !important;
                flex: none;
        .courselist
            display: flex;
            width: -moz-calc((100% - .91rem));
            width: -webkit-calc((100% - .91rem));
            width: calc(100% - .91rem);
            li
                flex: 1;
                font-size: .11rem;
                color: #263238;
                height: .6rem;
                .itemChild
                    height: .6rem;
                    display: flex;
                    flex-flow: column;
                    justify-content: center;
                    padding: .2rem;
                    border-right: 1px solid #EAECEF;
                    background: rgba(251, 252, 253, .3);
                .whiteColor
                    background: #fff;
                    border-bottom: 1px solid #eaeef0;
        .tableBody
            display: flex;
            .timeList
                border-left: 1px solid #eaeef0;
                width: 100%;
                .time-fir
                  width: .91rem !important;
                .rultlist
                  border-right: 1px solid #EAECEF;
                  display: table-cell;
                  vertical-align: middle;
                  border-right: 1px solid #EAECEF;
                  width: 13%;
                  li
                    margin-top: .1rem;
                    text-align: left;
                    margin-left: .2rem;
                    margin-bottom: .1rem;
</style>



